<template>
  <div class="component-home" ref="rootRef">
    <nav class="font-poppins">
      <ul>
        <li v-for="(n, idx) in state.navs" :class="{ active: n.active }" @click="move(n.name)" :key="idx">{{ n.name.substring(0, 1).toUpperCase() + n.name.substring(1) }}</li>
      </ul>
      <button class="btn" @click="callback('toggle-console')" :title="`${visible ? 'Close' : 'Open'} Console`">
        <span v-if="visible">CLOSE </span>
        <span v-else>OPEN </span>
        <span>CONSOLE</span>
      </button>
    </nav>
    <header>
      <div class="inner">
        <h1 class="font-parisienne color-point">Publessing</h1>
        <br />
        <h2 class="font-parisienne color-point">
          <b>Draw the UI with commands</b>
        </h2>
        <template v-if="login">
          <h3>
            Enter "<b>create startup</b>" <br />
            then "<b>open startup</b>"
          </h3>
        </template>
        <template v-else>
          <h3>
            Enter "<b>join</b>" to create account <br />
            or "<b>login</b>" to use the service
          </h3>
        </template>
        <button class="btn btn-point font-poppins" @click="callback('toggle-console')" :title="`${visible ? 'Close' : 'Open'} Console`">
          <span v-if="visible">CLOSE </span>
          <span v-else>OPEN </span>
          <span>CONSOLE</span>
        </button>
      </div>
    </header>
    <article>
      <div class="picture first font-poppins">
        <div class="wrapper">
          <div class="slogun">Draw web pages quickly</div>
          <div class="desc">This tool helps you to draw UI quickly and easily.</div>
        </div>
      </div>
      <div class="guide">
        <div class="wrapper">
          <div class="title font-poppins"># Guide</div>
          <div class="content">
            <h1 class="font-poppins">Getting Started</h1>
            <ul>
              <li>- 상단의 <b>OPEN CONSOLE</b> 버튼을 클릭합니다.</li>
              <li>- 명령창에 <b>join</b> 입력 후 엔터를 눌러 가입합니다.</li>
              <li>- 명령창에 <b>login</b> 입력 후 엔터를 눌러 로그인합니다.</li>
              <li>- 명령창에 <b>create startup</b> 입력 후 엔터를 눌러 페이지를 생성합니다.</li>
              <li>- 명령창에 <b>open startup1</b> 입력 후 엔터를 눌러 생성된 페이지를 오픈합니다.</li>
            </ul>
            <h1 class="font-poppins">Console Commands</h1>
            <div class="commands">
              <div class="command" v-for="(c, idx) in commands.console" :key="idx">
                <span class="badge">{{ c.cmd }}</span>
                <span>{{ c.desc }}</span>
                <span v-if="c.args.length">(args : {{ c.args.join(" ") }})</span>
              </div>
            </div>
            <h1 class="font-poppins">Component Commands</h1>
            <div class="commands">
              <div class="command" v-for="(c, idx) in commands.node" :key="idx">
                <span class="badge">{{ c.cmd }}</span>
                <span>{{ c.desc }}</span>
                <span v-if="c.args.length">(args : {{ c.args.join(" ") }})</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="picture second font-poppins">
        <div class="wrapper">
          <div class="slogun">Complete web page easily</div>
          <div class="desc">This tool provides variety components and templates.</div>
        </div>
      </div>
      <div class="contact">
        <div class="wrapper">
          <div class="title font-poppins"># Contact</div>
          <div class="content">
            <div class="row">
              <div class="col">
                <div class="form-group">
                  <label class="font-poppins" for="emailFrom">From</label>
                  <input type="email" class="form-control focus-border-pointer" id="emailFrom" placeholder="name@example.com" />
                </div>
              </div>
              <div class="col">
                <div class="form-group">
                  <label class="font-poppins" for="emailFromTo">To</label>
                  <input type="email" class="form-control focus-border-pointer" id="emailFromTo" value="africalibrary21@gmail.com" readonly />
                </div>
              </div>
            </div>
            <div class="form-group message">
              <label class="font-poppins" for="content">Message</label>
              <textarea class="form-control focus-border-pointer" id="content" placeholder="It's cool"></textarea>
            </div>
            <div class="actions">
              <button class="btn btn-point font-poppins">Submit</button>
            </div>
          </div>
        </div>
      </div>
    </article>
    <footer>
      <div class="wrapper">
        <a class="brand" href="/">Publessing</a>
        <div class="copyright">&copy; 2021. Publessing. All rights reserved.</div>
      </div>
    </footer>
  </div>
</template>
<script>
import { onMounted, reactive, ref } from "@vue/runtime-core";
import defines from "../defines";
import $ from "jquery";

export default {
  props: {
    login: Boolean,
    visible: Boolean,
    callback: Function,
  },
  setup() {
    const rootRef = ref();
    const state = reactive({
      navs: [
        { name: "home", active: true },
        { name: "guide", active: false },
        { name: "contact", active: false },
      ],
    });

    const previews = ["command", "code", "files", "components"];
    const definedCommands = defines.commands;

    const commands = {
      console: [],
      node: [],
    };

    const scrollTops = {
      home: 0,
      guide: 0,
      contact: 0,
    };

    const move = (name) => {
      const scrollTop = scrollTops[name];

      if (scrollTop === undefined) {
        return;
      }

      state.navs.forEach((x) => (x.active = false));
      state.navs.find((x) => x.name === name).active = true;

      $(rootRef.value)
        .stop()
        .animate({
          scrollTop,
        });
    };

    for (let i1 in definedCommands) {
      for (let i2 in definedCommands[i1]) {
        for (let c of definedCommands[i1][i2]) {
          commands[i1].push(c);
        }
      }
    }

    for (let i in commands) {
      commands[i].sort((a, b) => {
        if (a.cmd < b.cmd) {
          return -1;
        } else if (a.cmd > b.cmd) {
          return 1;
        }

        return 0;
      });
    }

    onMounted(() => {
      let timer;
      const navHeight = rootRef.value.querySelector("nav").offsetHeight;
      scrollTops.guide = rootRef.value.querySelector("article > .guide").offsetTop - navHeight + 1;
      scrollTops.contact = rootRef.value.querySelector("article > .contact").offsetTop - navHeight + 1;

      rootRef.value.addEventListener("scroll", function() {
        clearTimeout(timer);

        timer = setTimeout(() => {
          const nav = rootRef.value.querySelector("nav");
          const button = nav.querySelector("button");
          state.navs.forEach((x) => (x.active = false));

          button.classList.add("btn-point");

          if (rootRef.value.scrollTop >= scrollTops.contact) {
            state.navs.find((x) => x.name === "contact").active = true;
          } else if (rootRef.value.scrollTop >= scrollTops.guide) {
            state.navs.find((x) => x.name === "guide").active = true;
          } else {
            state.navs.find((x) => x.name === "home").active = true;
            button.classList.remove("btn-point");
          }
        }, 25);
      });
    });

    return { rootRef, state, previews, move, commands };
  },
};
</script>
